ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navbar a {
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.15em;

  display: inline-block;
  padding: 15px 20px;
  position: relative;
}

.aboutBtn a {
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.15em;

  display: inline-block;
  padding: 15px 20px;
  position: relative;
}

.navbar {
  margin-top: 5px;
  width: 100%;
}

.navbar a {
  padding: 10px 15px;
  text-transform: uppercase;
  text-align: center;
  display: block;
  font-size: .99em;
  color: #f2f2f2;
}

.header {
  padding-top: .5em;
  padding-bottom: .5em;
}


@media (min-width: 750px) {
  .header,
  .navbar {
    display: flex;
  }
  .header {
    float: right;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
  }

}

@media (min-width: 1000px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }

  .navbar a:after {
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 2px;
    left: 50%;
    position: absolute;
    background: #f2f2f2;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
  }
  .navbar a:hover:after {
    width: 100%;
    left: 0;
  }

  .homeP a:after {
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 2px;
    left: 50%;
    position: absolute;
    background: #f2f2f2;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
  }
  .homeP a:hover:after {
    width: 100%;
    left: 0;
  }
  .myImg {
    width: 400%;
    height: 60vh;
  }
  .row {
    display: flex;
  }
}

@media (orientation: portrait) {
  body {
    background-size: auto 100vh;
  }
  .myImg {
    width: 400%;
    height: 20vh;
  }

  .row:after {
    content: "";
    display: table;
    clear: both;
  }
}

.content {
  padding-top: 10%;
  width: 100%;
  display: flex;
  overflow: visible;
  justify-content:center; align-items:center;
}
.column {
  flex: 50%;
  align-content: center;
  text-align: left;
  width: 50%;
  padding: 40px;
}

.myImg {
  background-image: url("data:image/svg+xml,%3Csvg id='Layer_1' data-name='Layer 1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' viewBox='0 0 1080 904.67'%3E%3Cdefs%3E%3Cstyle%3E.cls-1%7Bfill:none;%7D.cls-2%7Bclip-path:url(%23clip-path);%7D%3C/style%3E%3CclipPath id='clip-path'%3E%3Cpath class='cls-1' d='M118.19,906.67C-41.94,632-36.2,312.16,116.27,142.67c244.7-272,837.16-123.35,945.8,222,79.09,251.37-127.79,512.77-149.94,540Z'/%3E%3C/clipPath%3E%3C/defs%3E%3Ctitle%3Eimg%3C/title%3E%3Cg class='cls-2'%3E%3Cimage width='1080' height='1077' transform='translate(0 -172.33)' xlink:href='data:image/jpeg;base64,'/%3E%3C/g%3E%3C/svg%3E");

  background-repeat: no-repeat;
}



@media screen and (max-width: 600px) {
  .column {
    width: 100%;
  }
}


.bef-wrapper {
  width: 100%;
  display: flex;
  justify-content:center; align-items:center;
}

.wrapper{
  width: 1600px;
  color: #222222;
  background: #ffffff;
  border-radius: 25px;
  margin-top: 3%;
  overflow:hidden;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.wrapper-home{
  width: 1600px;
  border-radius: 25px;
  margin-top: 3%;
  overflow:hidden;
}
h1 {
  color: #636363;
}

img {
  max-width: 500px;
  max-height: 500px;
  display:block;
  margin:auto;
}
.shadows:hover {
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.skills  li{
  margin-bottom: 3%;
  margin-right: 3%;
  display: inline-block;
  padding: 20px 40px;
  color: #f2f2f2;
  background-image: linear-gradient(60deg, #3400A1, #90009B);
}

.wrapper a {
  background-image: linear-gradient(60deg, #3400A1, #90009B);
  color: #f2f2f2;
  transition: box-shadow .3s;
}

.wrapper a:hover {
  box-shadow: 0 0 60px rgba(33,33,33,.2);
}

.projectHeader {
  text-align: center;
}

.wrapper-proj a {
  padding: 0;
  color: #f2f2f2;
  background: transparent;
}

.wrapper-proj {
  float: initial !important;
}

form, form .form-group {
  display: flex;
  flex-direction: column;
}
form {
  width: 100%;
}
form .form-group {
  margin-bottom: 3rem;
}
form label {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  font-weight: 400;
}

form input {
  width: 30rem;
}
input {
  background: none;
  font-size: 1.5rem;
  color: #3400A1;
  border: none!important;
  border-bottom: 2px solid #3400A1!important;
}

form textarea {
  width: 30rem;
}
textarea {
  outline: none!important;
  background: none;
  font-size: 1.5rem;
  color: #3400A1;
  border: 2px solid #3400A1!important;
  padding: 1rem;
  resize: vertical;
}

form input[type=submit], .submitBtn {
  height: 3rem;
  width: 30rem;
  background-color: #3400A1;
  color: white;
  font-size: 1.5rem;
  border-radius: .5rem;
  font-weight: 400;
  margin-bottom: 2rem;
  cursor: pointer;
}
form input, .submitBtn {
  width: 30rem;
}
.submitBtn {
  background: none;
  font-size: 1.5rem;
  color: #3400A1;
  border: none!important;
  border-bottom: 2px solid #3400A1!important;
}

.homeH2 {
  font-size: 60px;
}

.homeP {
  font-size: 30px;
}

.homeP a {
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.15em;
  display: inline-block;
  padding: 15px 20px;
  position: relative;
  text-align: center;
  font-size: .99em;
  color: #f2f2f2;
}
